import mpopup from '@/components/xuan-popup/xuan-popup.vue'
export default {
	components: { mpopup },
	data() {
		return {
			loginPhone:'',
			loginPassword:'',
		}
	},
	created() {
		var that = this
		uni.getStorage({
		    key: 'Logo',
		    success: function (res) {
				that.loginPhone = res.data.split("//")[0]
				that.loginPassword = res.data.split("//")[1]
				that.Logo()
		    }
		});
	},
	mounted() {},
	methods: {
		gcs(){//工程师测试账号
			this.loginPhone = 'gcs', this.loginPassword = '123456',
			this.Logo()
		},
		qyjl(){//区域经理测试账号
			this.loginPhone = 'quyujingli', this.loginPassword = '123456',
			this.Logo()
		},
		Logo(){
			//请稍等提示词
			// this.$refs.mpopup.open({  type:'loading', content:'请稍等...', timeout:100000,  });
			//调用登录接口
			uni.showLoading({title: '正在登陆'}); 
			uni.request({
			    url: this.$store.state.url+'Account/Login', 
				method:'POST',
			    data: {
						username: this.loginPhone,
						password: this.loginPassword,
			    },
			    success: (res) => {
			        var asd = JSON.parse(res.data)
					if(asd.Code == 0){
						//将账号储存在本地 
						uni.setStorage({
						    key: 'Logo',
						    data: this.loginPhone+'//'+this.loginPassword,
						    success: function () {}
						});
						//将Token储存在vuex当中
						this.$store.state.token = asd.Data
						//登录成功提示
						this.$refs.mpopup.open({ type:'success', content:'登录成功', timeout:2000, });
						//成功后将名字储存在Vuex内
						this.$store.state.username = this.loginPhone
						//跳转至主页
						uni.switchTab({
							url:'/pages/home/home'
						})
						setTimeout(function () {uni.hideLoading();}, 100);  
					}else{
						//登录失败提示
						uni.showLoading({title: '错误'}); 
						setTimeout(function () {uni.hideLoading();}, 2000);  
					}
			    }
			});
		}
	}
}
